      dialog: false,
      titre_viewer: null,
      projects: [
        { id: 1, title: '3D Space Ship', type: 'games', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=15', img_lazy: 'https://picsum.photos/10/6?image=15', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 2, title: '3D Space Ship', type: 'web', icon: 'web', img: 'https://picsum.photos/500/300?image=25', img_lazy: 'https://picsum.photos/10/6?image=25', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 3, title: '3D Space Ship', type: 'design', icon: 'palette', img: 'https://picsum.photos/500/300?image=35', img_lazy: 'https://picsum.photos/10/6?image=35', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 4, title: '3D Space Ship', type: 'games', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=45', img_lazy: 'https://picsum.photos/10/6?image=45', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 5, title: '3D Space Ship', type: 'games', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=55', img_lazy: 'https://picsum.photos/10/6?image=55', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 6, title: '3D Space Ship', type: 'games', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=65', img_lazy: 'https://picsum.photos/10/6?image=65', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 7, title: '3D Space Ship', type: 'web', icon: 'web', img: 'https://picsum.photos/500/300?image=75', img_lazy: 'https://picsum.photos/10/6?image=75', text: 'Description courte du projet', description: 'Description plus longue du projet' }
      ],
      projectsShown: []
    }
  },
  methods: {
    openDialog (n, title) {
      this.titre_viewer = title
      this.dialog = true
      document.getElementsByClassName('hover-menu')[n - 1].classList.add('opened')
      document.getElementsByClassName('open-viewer')[n - 1].classList.add('opened')
      this.changeHeight()
    },
    changeHeight () {
      if (window.innerWidth < 960) {
        document.getElementById('main-height').style.height = (window.innerWidth * 0.4) * (4 / 3) + 'px'
      } else {
        document.getElementById('main-height').style.height = (window.innerWidth * 0.4) * (9 / 16) + 'px'
      }
    },
    filterProjects (filter) {
      for (let item in this.projectsShown) {
        if (this.projectsShown[item].type !== filter) {
          this.projectsShown.splice(item, 1)
          this.filterProjects(filter)
        }
      }

      if (this.projectsShown.length === 0) {
        for (let item in this.projects) {
          if (this.projects[item].type === filter) {
            this.projectsShown.splice(item, 0, this.projects[item])
          }
        }
      }
    },
    resetProjects () {
      this.projectsShown = []
      for (let item in this.projects) {
        this.projectsShown.splice(item, 0, this.projects[item])
      }
    }
  },
  mounted () {
    for (let item in this.projects) {
      this.projectsShown.push(this.projects[item])
    }
    this.changeHeight()
    window.addEventListener('resize', this.changeHeight)
  }
}
</script>

<style scoped>
  .boutons button {
